/* StockList.css */

/* Define styles for the stock table */
.stock-table {
  width: 100%; /* Set the table width to 100% */
  border-collapse: collapse; /* Collapse the table borders */
}

/* Define styles for table header and table data cells */
.stock-table th,
.stock-table td {
  padding: 8px; /* Set padding for cells */
  border: 1px solid #ddd; /* Set border for cells */
  text-align: center; /* Align content to the center by default */
}

/* Align the content of the first column (Stock Code) to the left */
.stock-table th:first-child,
.stock-table td:first-child {
  text-align: left;
}

/* Define background color for table header */
.stock-table th {
  background-color: #f2f2f2; /* Set background color */
}

/* Define background color for even rows in table body */
.stock-table tbody tr:nth-child(even) {
  background-color: #f2f2f2; /* Set background color */
}

/* Define background color for hovered rows in table body */
.stock-table tbody tr:hover {
  background-color: #ddd; /* Set background color */
}
