/* style.css */

/* Container styles */
.container {
  max-width: 800px; /* Set maximum width */
  margin: 0 auto; /* Center align horizontally */
  padding: 20px; /* Add padding */
}

/* Heading styles */
h1 {
  font-size: 2.5rem; /* Set font size */
  font-weight: bold; /* Set font weight */
  text-align: center; /* Align text to center */
  margin-bottom: 20px; /* Add margin at bottom */
}

/* Link styles */
a {
  color: #4f46e5; /* Set link color */
  text-decoration: none; /* Remove underline */
}

a:hover {
  text-decoration: underline; /* Add underline on hover */
}

/* Stock list styles */
ul {
  list-style: none; /* Remove default list style */
  padding: 0; /* Remove padding */
}

li {
  margin-bottom: 10px; /* Add margin at bottom */
}

/* Stock detail styles */
.stock-detail {
  margin-top: 20px; /* Add margin at top */
}

/* Add stock form styles */
.add-stock-form {
  margin-top: 20px; /* Add margin at top */
}

.add-stock-form input[type="text"] {
  width: 100%; /* Set input width to 100% */
  padding: 8px; /* Add padding */
  margin-bottom: 10px; /* Add margin at bottom */
}

.add-stock-form button {
  padding: 8px 16px; /* Add padding */
  background-color: #4f46e5; /* Set background color */
  color: #fff; /* Set text color */
  border: none; /* Remove border */
  cursor: pointer; /* Set cursor to pointer */
}

.add-stock-form button:hover {
  background-color: #4338ca; /* Change background color on hover */
}
